.header {
    height: 108px;
    background-color: #aa0601;
}

.header-logo {
    padding: 30px 88px;
}

.logo a {
    background: url(../images/logo.png);
    background-repeat: no-repeat;
    background-size: contain;
    text-indent: 101%;
    overflow: hidden;
    display: block;
    white-space: nowrap;
    width: 5rem;
    height: 3.04rem;
}

.header-logo h1 {
    float: left;
}